import axios from 'axios'
import { createStore } from 'vuex'

const store = createStore({
    state() {
        return {
            name: 'kimdd',
            age: 2034,
            likes: 30,
            좋아요눌렀니: false,
            more: {},
        }
    },
    mutations: {
        setMore(state, data) {
            state.more = data
        },
        이름변경(state) {
            state.name = 'park'
        },
        인크리스(state, payload) {
            state.age += payload
        },
        좋아요쁠(state) {
            if (state.좋아요눌렀니) {
                state.likes--
                state.좋아요눌렀니 = 0
            } else {
                state.likes++
                state.좋아요눌렀니 = 1
            }

        }
    },
    actions: {
        getData(context) {
            axios.get('https://codingapple1.github.io/vue/more0.json').then((a) => {
                console.log(a.data);
                context.commit('setMore', a.data)
            })
        }
    },

})

export default store;